import React from "react";
import navbar from './navbar.css'
import { Button, Navbar, Container, Nav } from 'react-bootstrap'

export function ExNavbarComponent () {
    const login = () => window.location.href = 
    // 'http://localhost:3001/api/auth/discord' 
    'https://sevinco.herokuapp.com/api/auth/discord'

    const supportServer = () => window.open("https://discord.gg/Yf7rFZD9KF", "_blank")

    React.useEffect(() => {
        const navbar = document.getElementById('navber')

        function navbarPos() {
            let navPos = window.scrollY

            if (navPos < 665.3333129882812) {
                navbar.className = 'navbar'
            }
            if (navPos > 665.3333129882812) {
                navbar.className = 'nav-fixed'
            }
        }

        function scrollHandler() {
            let element = document.getElementById('header');
            let distanceToTop = window.pageYOffset + element.getBoundingClientRect().top;
            let elementHeight = element.offsetHeight;
            let scrollTop = document.documentElement.scrollTop;
            let opacity = 1;
            
            if (scrollTop > distanceToTop) {
                opacity = 1 - (scrollTop - distanceToTop) / elementHeight;
            }
            
            if (opacity >= 0) {
                element.style.opacity = opacity;
            }
        }

        window.addEventListener('scroll', () => {
            scrollHandler()
            navbarPos()
        });
    })

    return (
        <div id="navber" className="navbar" style={navbar}>
            <Navbar collapseOnSelect expand="lg" variant="dark">
                <Container>
                {/* <Navbar.Brand href="#home">
                    <img
                    alt="Sevinco"
                    src="https://cdn.discordapp.com/attachments/747451632534683679/904995247510151198/SevincoMusic.png"
                    width="90"
                    height="72"
                    className="d-inline-block align-top"
                    />
                </Navbar.Brand> */}
                <div className="logo">
                        <a href="/" className="sevinco">.sevinco</a>
                </div>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                    <Nav.Link onClick={
                        function () {
                            supportServer()
                        }
                    }>Support Server</Nav.Link>
                    {/* <Nav.Link href="#pricing">About</Nav.Link> */}
                    </Nav>
                    <Nav>
                        <Button onClick={login} variant="light" size='lg' style={{ color: 'white', background: `linear-gradient(to right, #ff1261, #fc4a42)`, border: '0px', fontFamily: 'monospace', fontWeight: '700', fontSize: '1.5rem'}}>Login</Button>
                    </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </div>
    )
}