import React from 'react';
import './style.css'
import { getUserDetails } from "../../utils/api";
import { ReactComponent as Logo } from '../../images/back.svg'
import { Button } from 'react-bootstrap'
import { ExNavbarComponent, NavbarComponent } from '../../components'

export function LandingPage () {

    const [user, setUser] = React.useState(null)
    const [logged, setLogged] = React.useState(false)

    React.useEffect(() => {
        getUserDetails()
            .then(({ data }) => {
                console.log(data)
                setUser(data)
                setLogged(true)
            }).catch((err) => {
                setLogged(false)
            })
    }, [])
    return (
        <>
        <div id="head" className="headers-container">
            <header id="header" className="header-container">
                <div className="brand_name">
                    <div className="tp-bg">
                        <img width={'100%'} height={'100%'} src="https://undsgn.com/wp-content/uploads/2018/04/ltotbngnzzu.jpg" alt="" />
                    </div>
                    <div className="sky-container">
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                        <div className="star"></div>
                      </div>
                    <div className="back">
                        <Logo width={400} height={400}/>
                    </div>
                    <a href="#nav" className="Sevinco">sevinco</a>
                    <a href="#nav" className="Bro">bro</a>
                </div>
            </header>
        
            <header id="nav">
                {logged ? (
                    <NavbarComponent user={user}/>
                ) : (
                    <ExNavbarComponent/>
                )}
            </header>
        </div>

        <footer style={{height: '100vh'}}></footer>
        </>
    )
}