import React from "react";
import navbar from './navbar.css'
import { NavDropdown, Navbar, Container, Nav, Button } from 'react-bootstrap'

export function NavbarComponent ({
    user
}) {

    React.useEffect(() => {
        const navbar = document.getElementById('navber')

        function navbarPos() {
            let navPos = window.scrollY

            if (navPos < 665.3333129882812) {
                navbar.className = 'navbar'
            }
            if (navPos > 665.3333129882812) {
                navbar.className = 'nav-fixed'
            }
        }

        function scrollHandler() {
            let element = document.getElementById('header');
            let distanceToTop = window.pageYOffset + element.getBoundingClientRect().top;
            let elementHeight = element.offsetHeight;
            let scrollTop = document.documentElement.scrollTop;
            let opacity = 1;
            
            if (scrollTop > distanceToTop) {
                opacity = 1 - (scrollTop - distanceToTop) / elementHeight;
            }
            
            if (opacity >= 0) {
                element.style.opacity = opacity;
            }
        }

        window.addEventListener('scroll', () => {
            scrollHandler()
            navbarPos()
        });
    })

    const supportServer = () => window.open("https://discord.gg/Yf7rFZD9KF", "_blank")

    const addServer = () => window.location.href = 
    // "https://discord.com/api/oauth2/authorize?response_type=code&redirect_uri=http%3A%2F%2Flocalhost:3001%2Fapi%2Fauth%2Fdiscord%2Fredirect&client_id=903973834892382249&permissions=139589930240&scope=bot%20applications.commands"
    "https://discord.com/api/oauth2/authorize?response_type=code&redirect_uri=https%3A%2F%2Fsevinco.herokuapp.com%2Fapi%2Fauth%2Fdiscord%2Fredirect&client_id=903973834892382249&permissions=139589930240&scope=bot%20applications.commands"

    return (
        <div id="navber" className="navbar" style={navbar}>
            <Navbar collapseOnSelect expand="lg" variant="dark">
                <Container>
                {/* <Navbar.Brand href="#home">
                    <img
                    alt="Sevinco"
                    src="https://cdn.discordapp.com/attachments/747451632534683679/904995247510151198/SevincoMusic.png"
                    width="90"
                    height="72"
                    className="d-inline-block align-top"
                    />
                </Navbar.Brand> */}
                <div className="logo">
                        <a href="/" className="sevinco">.sevinco</a>
                </div>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                    <Nav.Link onClick={
                        function () {
                            supportServer()
                        }
                    }>Support Server</Nav.Link>
                    {/* <Nav.Link href="#pricing">About</Nav.Link> */}
                    </Nav>
                    <Nav>
                        {/* <Nav>
                            <img src={`https://cdn.discordapp.com/avatars/${user.discordId}/${user.avatar}.png?size=44`} className="avatar" alt=" " />
                        </Nav> */}
                    <NavDropdown title={user.discordUser} id="collasible-nav-dropdown">
                        <NavDropdown.Item href="/menu">Servers</NavDropdown.Item>
                        <NavDropdown.Item onClick={
                            function () {
                                addServer()
                            }
                        }>Add to Server</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item style={{color: "red"}} >Log Out</NavDropdown.Item>
                    </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </div>
    )
}